.admin-container {
    width: 95%;
    margin: 0 auto;
    font-family: Arial, sans-serif;
    border-radius: 8px;
    padding: 5px 0;
}

.tab-menu {
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid #ffcc00;
}

.tab-menu button {
    flex: 1;
    padding: 10px 20px;
    font-size: 18px;
    color: #333;
    background-color: #ffeb99;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
}

.tab-menu button.active {
    background-color: #ffd633;
    font-weight: bold;
}

.tab-menu button:hover {
    background-color: #ffcc00;
}

.admin-table-container {
    max-height: 520px; /* 테이블의 최대 높이 설정 */
    overflow-y: auto; /* 세로 스크롤 추가 */
    border: 1px solid #ddd;
    padding: 0 5px;
    background-color: #fff;
}

.table-controls {
    display: flex;
    justify-content: space-between;
    margin: 10px 0 15px 0;
}

.search-input {
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    width: 40%;
}

.control-button {
    padding: 10px 15px;
    background-color: #ffcc00;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s;
}

.control-button:hover {
    background-color: #ffaa00;
}

.admin-table {
    width: 100%;
    border-collapse: collapse;
    text-align: center;
}

.admin-table th, .admin-table td {
    border: 1px solid #7c7c7c;
    padding: 10px;
    font-size: 14px;
}

.admin-table th {
    background-color: #ffcf61;
    color: #fff;
    position: sticky; /* 스크롤 시 상단 고정 */
    top: 0;
    z-index: 1; /* 헤더가 내용 위로 오도록 설정 */
    padding: 10px;
}

.admin-table td {
    position: relative;
}

.pagination {
    display: flex;
    justify-content: center;
    margin-top: 15px;
}

.pagination button {
    background-color: #ffcc00;
    border: none;
    padding: 5px 10px;
    margin: 0 5px;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s;
    color: #fff;
}

.pagination button.active {
    background-color: #ffaa00;
    font-weight: bold;
}

.pagination button:hover:not(.active) {
    background-color: #ffaa00;
}

.pagination button:disabled {
    background-color: #f0f0f0;
    color: #ccc;
    cursor: not-allowed;
}

.manage-button {
    position: absolute;
    width: 80px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #ffcc00;
    border: none;
    margin: 0;
    border-radius: 4px;
    padding: 5px 10px;
    font-size: 14px;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s;
}

.manage-button:hover {
    background-color: #ffaa00;
    color: #fff;
}

.table-controls {
    display: flex;
    justify-content: space-between;
    margin: 10px 0 15px 0;
    align-items: center;
}

.control-buttons {
    display: flex;
    gap: 10px;
}

.control-button {
    padding: 10px 15px;
    background-color: #ffcc00;
    border: none;
    border-radius: 4px;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s;
}

.control-button:hover {
    background-color: #ffaa00;
}